/* comm css */
.form {
    padding-bottom: 30px;
    margin: 25px 10px;
}
.content {
    width: 90%;
    margin: 10px auto;
    text-align: center;
}
.main-content .title {
    background: #ED2424;
}
.main-content .title p {
    color: #fff;
}
.form input {
    margin-top: 15px;
    border: 0;
    border-bottom: 1px solid #ED2424;
}
.form input:focus {
    border-bottom: 2px solid #ED2424;
}
.form input, .form button,.main-content button {
    display: block;
    margin: 15px auto;
    padding: 8px 15px;
    width: 100%;
}
.btn-com {
    display: block;
    margin-top: 20px !important;
    padding: 5px 0;
    width: 100%;
    background-color: #ED2424;
    color: #fff;
    border: 0;
    border-radius: 3px;
}
.btn-com a {
    display: block;
    color: #fff;
}
.cl-white {
    color: white;
}
.bk-btn {
    display: inline;
    padding-left: 2%;
}
/* comm css */
.signin .title {
    font-size: 1.2rem;
    line-height: 1;
}
.btn-m-t {
    margin-top: 35px !important;
}
/* Balance */
.balance .list {
    margin: 0 auto;
    width: 96%;
}
.balance .list li {
    padding: 1px 3px;
    background: #ED2424;
    margin: 8px 0;
    border-radius: 1px;
}
.balance .list p {
    background: #fff;
    padding: 8px;
}
.balance {
    height: 88vh;
    overflow: auto;
}
.balance::-webkit-scrollbar{
    width :0px;
}
.balance .app {
    position: fixed;
    right: 2%;
    bottom: 2%;
    font-size: 2.5rem;
}
.balance .history-list {
    position: fixed;
    left: 2%;
    bottom: 2%;
    font-size: 2.5rem;
}
.app a,.history-list a {
    color: black;
}
.history {
    margin: 0 auto;
    padding: 10px 0;
    width: 96%;
}
.history .detail {
    display: block;
    overflow: auto;
    margin-bottom: 15px;
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-left: 4px solid red;
    line-height: 1.7;
    border-radius: 3px;
    box-shadow: 0px 3px #dddddd61;
}
